<template>
    <button class="button"
            v-bind:class="{ 'button--transparent': isTransparent }"
            v-bind:type="type"
            v-on:click="handleClick">
        <slot></slot>
    </button>
</template>

<script>
export default {
    name: 'item-button',

    props: {
        type: {
            type: String,
            default: 'text'
        },
        isTransparent: {
            type: Boolean,
            default: false
        }
    },

    methods: {
        handleClick () {
            this.$emit('click');
        }
    }
};
</script>

<style scoped>
.button {
    height: 24px;
    padding-top: 5px;
    padding-right: 10px;
    padding-bottom: 5px;
    padding-left: 10px;
    margin-right: 4px;
    margin-left: 0;
    border-width: 2px;
    border-style: solid;
    border-color: var(--color-button-border-active);
    border-radius: 2px;
    outline: none;
    font-size: var(--font-size-app-small);
    line-height: 1;
    background-color: var(--color-button-background-active);
    color: var(--color-button-text-active);
    cursor: pointer;
}

.button--transparent {
    display: inline-block;
    height: 28px;
    border: 0;
    background-color: transparent;
    transition: all 0.25s ease-in-out;
}

button:disabled {
    border-color: var(--color-button-border-disabled);
    background-color: var(--color-button-background-disabled);
    color: var(--color-button-text-disabled);
}

button:last-of-type {
    margin-right: 0;
}
</style>
